import React from "react";

export default function Heatmap(props) {

    function scale (number, inMin, inMax, outMin, outMax) {
        return (number - inMin) * (outMax - outMin) / (inMax - inMin) + outMin;
    }
    // bg EDF0F3
    // text 73828C
    //
    // bg color: 11191f
    // dark color: 333333
    // fill color 546e7a
    // need to flip, but on the right track
    // var obs = [1,0,6.3,2.12,1,0,9.9,0,3,1];
    var obs = props.activities;
    if (!obs) obs = [0];
    // console.log(obs);
    var npad = new Date(2022,0).getDay(); // day of week, n to add before
    //var dd = Array(npad).fill(0).forEach(z => obs.unshift(z));
    var nwk = 53;
    var blocks = [];
    for(let dow = 0; dow < 7; dow++){
      for(let wk = 0; wk < nwk; wk++){
    	let i = (wk*7) + (dow%7);
        var blockColor;
        var block;
        if(i < npad){
            // console.log(i,'blank');
            blockColor = '#ffffff';
            block = <div key={i} className="block" style={{background:blockColor}}></div>
        } else if(obs[i-npad] > 0){
            // console.log(i, obs[i-npad]);
            let ll = scale(obs[i-npad],0,Math.max(...obs),90,50) | 0;
            blockColor = 'hsl(42, 100%, '+ll.toString()+'%)';
            // console.log(blockcolor)
            // console.log('hsl(199, 18%, '+ll.toString()+'%)');
            // blockColor = 'hsl(199, 18%, 40%)';
            //hsl(199, 18%, 40%)
            var obsDate = new Date(new Date(2022,0).setDate(i-npad+1)).toISOString().substring(0,10);
            var tooltip = obsDate+': '+(obs[i-npad]/1609.34).toFixed(1)+'mi';
            console.log(tooltip);
            block = <div key={i} className="block" style={{background:blockColor, borderBottom:'none'}} data-tooltip={tooltip}></div>
        } else {
            blockColor = '#EDF0F3'; // 141e26
            block = <div key={i} className="block" style={{background:blockColor}}></div>
        }
        blocks.push(block);
      }
    }

    // for (var i = 0; i < 365; i++){
    //     var blockColor;
    //     if(i < npad){
    //         blockColor = '#11191f';
    //     } else if(obs[i-npad] > 0){
    //         blockColor = '#546e7a';
    //     } else {
    //         blockColor =  '#333333';
    //     }
    //     blocks.push(
    //         <div key={i} className="block" style={{background:blockColor}}></div>
    //     );
    // };

    return(
        <figure>
        <div className="contributions">
          <div className="spacer"></div>
          <div className="month five-weeks">jan</div>
          <div className="month">feb</div>
          <div className="month">mar</div>
          <div className="month five-weeks">apr</div>
          <div className="month">may</div>
          <div className="month">jun</div>
          <div className="month five-weeks">jul</div>
          <div className="month">aug</div>
          <div className="month">sep</div>
          <div className="month five-weeks">oct</div>
          <div className="month">nov</div>
          <div className="month five-weeks">dec</div>
          <div className="day">mon</div>
          <div className="day">wed</div>
          <div className="day">fri</div>
          {blocks}
        </div>
        </figure>
    );
}
