import React, { useEffect, useState } from 'react';
import { Connection, PublicKey, LAMPORTS_PER_SOL } from '@solana/web3.js';
import { Program, Provider, web3 } from '@project-serum/anchor';
// import './App.css';
import { isMobile } from 'react-device-detect';
import Heatmap from "./Heatmap";
import idl from './idl.json';
// SOL price: https://api.binance.com/api/v3/ticker/price?symbol=SOLUSDC

const { SystemProgram, Keypair, Transaction } = web3;
const programID = new PublicKey(idl.metadata.address);
const network = 'https://api.devnet.solana.com';
const opts = {preflightCommitment: "processed"}
const payee = new PublicKey('DYuixff6fkkkhrP8twEwWBkZuALyUeZAd4Cg8Vx5puGV');
const packCost = LAMPORTS_PER_SOL/10 - 6305760; // 0.1 sol minus rent cost
var datetoday = new Date(); // because of ISO weirdness
var today = datetoday.getFullYear()+'-'+(datetoday.getMonth()+1).toString().padStart(2,"0")+'-'+datetoday.getDate();


// const TEST_GIFS = [
// 	'https://i.giphy.com/media/eIG0HfouRQJQr1wBzz/giphy.webp',
// 	'https://media3.giphy.com/media/L71a8LW2UrKwPaWNYM/giphy.gif?cid=ecf05e47rr9qizx2msjucl1xyvuu47d7kf25tqt2lvo024uo&rid=giphy.gif&ct=g',
// 	'https://media4.giphy.com/media/AeFmQjHMtEySooOc8K/giphy.gif?cid=ecf05e47qdzhdma2y3ugn32lkgi972z9mpfzocjj6z1ro4ec&rid=giphy.gif&ct=g',
// 	'https://i.giphy.com/media/PAqjdPkJLDsmBRSYUp/giphy.webp'
// ]
// // Change this up to be your Twitter if you want.
// const TWITTER_HANDLE = '_buildspace';
// const TWITTER_LINK = `https://twitter.com/${TWITTER_HANDLE}`;

const App = () => {
    const [walletAddress, setWalletAddress] = useState(null);
    const [inputValue, setInputValue] = useState('');
    const [gifList, setGifList] = useState([]);
	const [inputDate, setInputDate] = useState(today);
	const [inputDist, setInputDist] = useState(1.0);
	const [inputUnit, setInputUnit] = useState('mi');
	const [loading, setLoading] = useState(false);
	const [balance, setBalance] = useState(0);
	const [activityAccount, setActivityAccount] = useState();
	const [activities, setActivities] = useState();

	const getProvider = () => {
		const connection = new Connection(network, opts.preflightCommitment);
		const provider = new Provider(connection, window.solana, opts.preflightCommitment,);
		return provider;
	}

	const checkIfWalletIsConnected = async () => {
		try {
			const { solana } = window;
			if (solana) {
				if (solana.isPhantom) {
					console.log('Phantom wallet found!');
					const response = await solana.connect({ onlyIfTrusted: true });
					console.log('Connected with Public Key:',response.publicKey.toString());
					setWalletAddress(response.publicKey.toString());
				}
			} else {
				console.log('Solana object not found! Get a Phantom Wallet ðŸ‘»');
			}
		} catch (error) {
			console.error(error);
		}
	};

    const connectWallet = async () => {
        const { solana } = window;
        if (solana) {
          const response = await solana.connect();
          console.log('Connected with Public Key:', response.publicKey.toString());
          setWalletAddress(response.publicKey.toString());
        }
    };

	const disconnectWallet = async () => {
		window.solana.disconnect();
		setWalletAddress(null);
		setActivityAccount(null);
		setActivities(null);
	};

	// APP FUNCTIONS
    const requestSol = () => {}
    const getActivity = async (pubKey) => {
      setLoading(true);
      try {
          const provider = await getProvider();
          const program = new Program(idl, programID, provider);
          const aa = await program.account.activity.all([
              {memcmp: {offset: 8, bytes: pubKey.toBase58(),}},
          ])
		  console.log(aa.length, aa);
          if(aa.length > 0){
              setActivityAccount(aa[0].publicKey);
              setActivities(aa[0].account.distance);
          }
      } catch (error) {
          console.log("error getting activity", error);
      }
      setLoading(false);
    };
    const addActivity = async () => {
		// get day of get day of year
		setLoading(true);
		let doy = (new Date(inputDate).getTime() - Date.UTC(datetoday.getFullYear(),0,1))/8.64e7 | 0;
		let meters = inputUnit == 'mi' ? inputDist*1609.34|0 : inputDist*1000|0;
		console.log("adding",doy, meters);
		const provider = getProvider();
		const program = new Program(idl, programID, provider);
		await program.rpc.addActivity(doy,meters, {
			accounts: {
				activity: activityAccount,
				author: provider.wallet.publicKey,
			},
		});
		setLoading(false);
		getActivity(provider.wallet.publicKey);
	}
    const makeActivity = async () => {
        setLoading(true);
        // try {
		const provider = getProvider();
		const program = new Program(idl, programID, provider);
		const newActivityAccount = Keypair.generate(); // account for the token pack
		let tx = new Transaction();
		tx.add(
			SystemProgram.transfer({
				fromPubkey: provider.wallet.publicKey,
				toPubkey: payee,
				lamports: packCost,
			}),
			program.instruction.makeActivity(2020, {
                accounts: {
                    activity: newActivityAccount.publicKey,
                    author: provider.wallet.publicKey,
                    systemProgram: SystemProgram.programId,
                }
            })
		);
		await program.provider.send(tx, [newActivityAccount]);
        console.log("success",newActivityAccount.publicKey);
        // } catch (error) {
            // console.log("Error in making activity pack");
        // }
        setLoading(false);
        // once txn is complete, get account again
		getActivity(provider.wallet.publicKey);
    }

    // const sendGif = async () => {
    //     if (inputValue.length > 0) {
    //       console.log('Gif link:', inputValue);
    //       setGifList([...gifList, inputValue]);
    //       setInputValue('');
    //     } else {
    //       console.log('Empty input. Try again.');
    //     }
    // };
    // const onInputChange = (event) => {
    //   const { value } = event.target;
    //   setInputValue(value);
    // };

	const deleteAcct = async () => {
		setLoading(true);
		// try {
		const provider = getProvider();
		const program = new Program(idl, programID, provider);
		await program.rpc.deleteActivity({
			accounts: {
				activity: activityAccount,
				author: provider.wallet.publicKey,
			},
		})
		setActivityAccount(null);
		setActivities(null);
		setLoading(false);
		getActivity(provider.wallet.publicKey);
	}

	const renderNotConnectedContainer = () => (
        <article>
          <p><strong>Connect with <img src="phantom-icon-purple.svg" width="30"/> Phantom</strong></p>
		  {isMobile ? (
			  <a href="https://phantom.app/ul/browse/https%3A%2F%2F137.184.49.77?ref=https%3A%2F%2F137.184.49.77" role="button"> Connect </a>
		  ) : (
			  <button onClick = {connectWallet}> Connect </button>
		  )}
        </article>
    );

	const renderConnectedContainer = () => (
      <div>
      {activityAccount ? (
          <div>
          <Heatmap activities={activities} />
          <article id="activityForm">
              <p><strong>Add Activity</strong></p>
              <div className = "grid">
                  <input type="date"
                      id="date" name="date"
                      defaultValue = {today}
                      onChange={(event) => setInputDate(event.target.value)}/>
                  <input type="number"
                      id="distance" name="distance"
                      defaultValue = "1.0"
                      onChange={(event) => setInputDist(event.target.value)}/>
                  <select id="metric"
                      value={inputUnit}
                      onChange={(event) => setInputUnit(event.target.value)}>
                    <option defaultValue="mi">mi</option>
                    <option defaultValue="km">km</option>
                  </select>
                  <button onClick={addActivity} aria-busy={loading} disabled={loading}>Add</button>
              </div>
          </article>
		  <button onClick={deleteAcct} aria-busy={loading}>Delete Account</button>
          </div>
      ) : (
          <article id="accountData">
              <p><strong>Account</strong></p>
              <p>Address: <code>{walletAddress}</code></p>
			  {
				  // <p>Balance: <code>{balance} SOL</code>
	              //     {balance === 0 && <a aria-busy={loading} onClick={requestSol} disabled={loading}>Airdrop test SOL</a>}
	              // </p>
			  }
              <button onClick={makeActivity} aria-busy={loading} disabled={loading}>Buy 2022 Pack (0.1 SOL)</button>
          </article>
      )}
      </div>
    );

    useEffect(() => {
		const onLoad = async () => {
			await checkIfWalletIsConnected();
		};
		window.addEventListener('load', onLoad);
		return () => window.removeEventListener('load', onLoad);
    }, []);

	useEffect(() => {
		if (walletAddress) {
			console.log('Getting data...');
			// Call Solana program here.
			let pk = new PublicKey(walletAddress);
			getActivity(pk);
		}
	}, [walletAddress]);

	return (
    <div>
		<nav className="container">
			<ul>
				<li><h1>ðŸ‘‹</h1></li>
			</ul>
			{walletAddress ? (
				<ul>
				<li>
					<a href="#" data-tooltip="Click to copy"><code onClick={() => navigator.clipboard.writeText(walletAddress)}>{walletAddress.substring(0,6)+'â€¦'+walletAddress.substring(38)}</code></a>
					&nbsp;
					<a href="#" onClick={disconnectWallet}><code>Disconnect</code></a>
				</li>
				</ul>
			):(<br/>)}
		</nav>
		<div className="container">
			<div className={walletAddress ? 'authed-container':'container'}>
				<div className="header-container">
				{walletAddress ? (
					renderConnectedContainer()
				) : (
					renderNotConnectedContainer()
				)}
				</div>
				<div className="footer-container">
				</div>
			</div>
		</div>
    </div>
    );
};

export default App;
